<template>
	<div class="one">
		<div id="one-data">
		     <p v-for="item in users">
		     	<span>{{item.name}}</span>
		     	<span>{{item.age}}</span>
		     	<span>{{item.body}}</span>
		     </p>
		   
		</div>
	</div>
</template>

<script>
	import axios from 'axios'
	export default {
	 name: 'hello',
	  data () {
	    return {
	         users:[]
	    }
	  },
	  mounted(){
	    var self=this;
	    axios({
	      url:"http://localhost:5555/getlist",
	      headers:{
	        'Content-Type':'application/json'
	      },
	      method:"get"
	    }).then((res)=>{
	      self.users=res.data.user
	      console.log(self.users)
	    })
	  }
	}
</script>

<style lang="scss" scoped="" type="text/css">

#one-data{
	width:100%;
	p{
		width:100%;
		display: flex;
		flex-wrap: wrap;
		justify-content: space-around;
		margin-top:1rem;
		span{
			color:sandybrown;
			font-size:1.4rem;
			margin-top:0.5rem;
		}
	}
	
}
</style>


